.bm-burger-button {
  position: absolute;
  top: 17px !important;
  cursor: pointer;
}
.bm-burger-bars {
  background-color: #3a7a7a;
}
.line-style {
  position: absolute;
  height: 20%;
  left: 0;
  right: 0;
}
.cross-style {
  position: absolute;
  top: 12px;
  right: 2px;
  cursor: pointer;
}
.bm-cross {
  background: #ffffff;
}
.bm-cross-button {
  height: 30px;
  width: 30px;
}
.bm-menu {
  height: 100%;
  /* 100% Full-height */
  width: 0;
  /* 0 width - change this with JavaScript */
  position: fixed;
  /* Stay in place */
  z-index: 1000;
  /* Stay on top */
  top: 0;
  left: 0;
  background-color: #009688 !important;
  overflow-x: hidden;
  /* Disable horizontal scroll */
  padding-top: 60px;
  /* Place content 60px from the top */
  -webkit-transition: 0.5s;
  transition: 0.5s;
  /*0.5 second transition effect to slide in the sidenav*/
  -webkit-box-shadow: 20px;
          box-shadow: 20px;
}
.bm-overlay {
  background: rgba(0, 0, 0, 0);
}
.bm-item-list {
  color: #ffffff;
  margin-left: 10%;
  font-size: 2em;
  /*material overwrite*/
  background-color: #009688;
  height: 0;
}
.bm-item-list > * {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0.7em;
}
.bm-item-list > * > span {
  margin-left: 10px;
  font-weight: 700;
  color: white;
}
.bm-item-list > * > span a:hover {
    color: black;
}

